import { Fragment, useState, useEffect } from 'react';
// import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { updateCategoryData } from '../../../../redux/pcategoryReducer';
import { openSnackBar } from '../../../../redux/snackBarReducer';
import { selectUser } from '../../../../redux/userReducer';

const AddCategoryModal = ({isModal, setIsModal}) => {
  // const navigate = useNavigate();
  const dispatch = useDispatch();
  const [cate, setCate] = useState('');
  const seluser = useSelector(selectUser);

  const handleConfirm = () => {
    if (cate.length === 0) {
        dispatch(openSnackBar({ message: "Please input category name.", status: 'error' }));  
        return;
    }

    dispatch(updateCategoryData(0, {name: cate, createdBy: seluser._id}))
    setCate('');
    setIsModal(false);
  }

  useEffect(() => {
    if (isModal) {
      setCate('');
    }
  }, [isModal])

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full" onKeyUp={(e) => {if (e.code === 'Enter' || e.code === 'NumpadEnter') { handleConfirm()}} }>
              <div className="relative bg-[#404040] w-[30vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className=''>
                  <h1 className='text-md text-left text-[#D9D9D9] select-none'>Please enter category name:</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[2rem] pb-[0.5rem]">
                  <input 
                    className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                    type="text"
                    value={cate}
                    onChange={e => setCate(e.target.value)} />
                </div>
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Confirm</button>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default AddCategoryModal;