import {useState, useEffect} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import AddCategoryModal from './modals/add.category.modal';
import AddProductModal from './modals/add.product.modal';
import EditCategoryModal from './modals/edit.category.modal';
import EditProductModal from './modals/edit.product.modal';
import DeleteCategoryModal from './modals/delete.category.modal';
import DeleteProductModal from './modals/delete.product.modal';
import { selectCategories, getCategories } from '../../../redux/pcategoryReducer';
import { selectProducts, getProducts } from '../../../redux/productReducer';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';


const Inventory = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [categoryModal, setCategoryModal] = useState(false);
  const [productModal, setProductModal] = useState(false);
  const [editCategoryModal, setEditCategoryModal] = useState(false);
  const [editProductModal, setEditProductModal] = useState(false);
  const [deleteCategoryModal, setDeleteCategoryModal] = useState(false);
  const [deleteProductModal, setDeleteProductModal] = useState(false);
  const [selCategory, setSelCategory] = useState([]);
  const [selProduct, setSelProduct] = useState([]);
  const categories = useSelector(selectCategories);
  const products = useSelector(selectProducts);

  useEffect(() => {
    dispatch(getCategories());
    dispatch(getProducts());
  }, [])

  const categoryName = (id) => {
    const cate = categories.filter((c) => c._id === id);
    return cate[0] ? cate[0].name : '';
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Inventory</p>
              </div>
              <div className='w-[50%] flex px-[2rem] mt-[2rem] text-gray-100 text-xl italic'>
                <button 
                    className={`w-[10rem] text-md leading-[2.1rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
                    onClick={() => setCategoryModal(true)}
                >Add Category</button>
                <button 
                    className={`w-[10rem] text-md leading-[2.1rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 ml-6`}
                    onClick={() => setProductModal(true)}
                >Add Product</button>
              </div>
              <div className='text-md divide-y divide-gray-400 py-1 mt-2 px-[2rem]'>
                <div className='text-xl font-bold uppercase text-gray-100 mb-2'>Categories</div>
                <div className='h-[28vh] overflow-y-auto'>
                  {
                    categories.map((c, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem]'>
                          <p className='w-[70%]'>{c.name}</p>
                          <p className='w-[15%] text-center select-none cursor-pointer' onClick={() => {setEditCategoryModal(true); setSelCategory(c);}}>Edit</p>
                          <p className='w-[15%] text-center select-none cursor-pointer' onClick={() => {setDeleteCategoryModal(true); setSelCategory(c);}}>Delete</p>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
              <div className='text-md divide-y divide-gray-400 py-1 mt-2 px-[2rem]'>
                <div className='text-xl font-bold uppercase text-gray-100 mb-2'>PRODUCTS</div>
                <div className='h-[28vh] overflow-y-auto'>
                  {
                    products.map((p, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem]'>
                          <p className='w-[70%]'>{p.name}{' > '}{categoryName(p.categoryId)}</p>
                          <p className='w-[15%] text-center select-none cursor-pointer' onClick={() => {setEditProductModal(true); setSelProduct(p);}}>Edit</p>
                          <p className='w-[15%] text-center select-none cursor-pointer' onClick={() => {setDeleteProductModal(true); setSelProduct(p);}}>Delete</p>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
            </div>
            <Sidebar />
        </div>
        <AddCategoryModal isModal={categoryModal} setIsModal={setCategoryModal} />
        <AddProductModal isModal={productModal} setIsModal={setProductModal} categories={categories} />
        <EditCategoryModal isModal={editCategoryModal} setIsModal={setEditCategoryModal} category={selCategory} setCategory={setSelCategory}/>
        <EditProductModal isModal={editProductModal} setIsModal={setEditProductModal} categories={categories} product={selProduct} setProduct={setSelProduct}/>
        <DeleteCategoryModal isModal={deleteCategoryModal} setIsModal={setDeleteCategoryModal} category={selCategory}/>
        <DeleteProductModal isModal={deleteProductModal} setIsModal={setDeleteProductModal} product={selProduct}/>
    </Layout>
  );
}

export default Inventory;