import { Fragment, useState, useEffect } from 'react';
// import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { updateProductData } from '../../../../redux/productReducer';
import { uploadService } from '../../../../services/upload.service';
import { selectUser } from '../../../../redux/userReducer';

const EditProductModal = ({isModal, setIsModal, categories, product, setProduct}) => {
  // const navigate = useNavigate();
  const dispatch = useDispatch();

  const [name, setName] = useState('');
  const [desc, setDesc] = useState('');
  const [price, setPrice] = useState(0);
  const [categoryID, setCategoryID] = useState('');
  const [sku, setSKU] = useState('');
  const [photo, setPhoto] = useState('');
  const seluser = useSelector(selectUser);

  const handleConfirm = () => {    
    dispatch(updateProductData(product._id, {
      name: name,
      desc: desc,
      price: price,
      sku: sku,
      photo: photo,
      categoryId: categoryID,
      createdBy: seluser._id
    }))
    setName('');
    setDesc('');
    setPrice(0);
    setSKU('');
    setPhoto('');
    setCategoryID(categories[0]._id);
    setProduct([]);
    setIsModal(false);
  }

  const handleSelect = (e) => {
    setCategoryID(e.target.value);
  }

  useEffect(() => {
    if (categories[0]) {
      setCategoryID(categories[0]._id)
    }
  }, [categories[0]])

  useEffect(() => {
    if (product && product._id) {
      setName(product.name);
      setDesc(product.description);
      setPrice(product.price);
      setCategoryID(product.categoryId);
      setSKU(product.sku);
      setPhoto(product.photo);
    }
  }, [product._id])

  const onClickPhoto = (e) => {
    document.getElementById('image_photo')?.click()
  }

  const onChangePhoto = async (e) => {
    if (e.target.files && e.target.files.length > 0) {
      let file = document.getElementById("image_photo").files[0];
      let formData = new FormData();        
      formData.append("file", file);
      formData.append("name", file.name);
      const ret = await uploadService.uploadFiles(formData);
      if (ret.status === "success")
        setPhoto(ret.result)
    }
  }

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full" onKeyUp={(e) => {if (e.code === 'Enter' || e.code === 'NumpadEnter') { handleConfirm()}} }>
              <div className="relative bg-[#404040] w-[30vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className=''>
                  <h1 className='text-md text-left text-[#D9D9D9] select-none'>Product Name:</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[2rem] pb-[0.5rem]">
                  <input 
                    className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                    type="text"
                    value={name}
                    onChange={e => setName(e.target.value)}  />
                </div>
                {/* TITLE */}
                <div className=''>
                  <h1 className='text-md text-left text-[#D9D9D9] select-none'>Description:</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[2rem] pb-[0.5rem]">
                  <input 
                    className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                    type="text"
                    value={desc}
                    onChange={e => setDesc(e.target.value)}  />
                </div>
                <div className='flex min-h-[7rem] gap-4'>
                  <div className='w-[40%]'>
                    {/* TITLE */}
                    <div className=''>
                      <h1 className='text-md text-left text-[#D9D9D9] select-none'>Price:</h1>
                    </div>
                    {/* CONTENTS */}
                    <div className="min-h-[2rem] pb-[0.5rem]">
                      <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                        type="number"
                        value={price}
                        onChange={e => setPrice(e.target.value)}  />
                    </div>
                  </div>
                  <div className='w-[60%]'>
                    {/* TITLE */}
                    <div className=''>
                      <h1 className='text-md text-left text-[#D9D9D9] select-none'>Category:</h1>
                    </div>
                    {/* CONTENTS */}
                    <div className="min-h-[2rem] pb-[0.5rem]">
                      <select
                        className="bg-[#404040] border-b border-gray-300 text-gray-100 text-sm block w-full p-2.5 focus:outline-none"
                        onChange={(e) => handleSelect(e)}
                      >
                        {
                          categories.map((c, index) => {
                            return (
                              <option key={index} value={c._id}>{c.name}</option>
                            )
                          })
                        }
                      </select>    
                    </div>
                  </div>
                </div>
                <div className='flex min-h-[8rem] gap-4'>
                  <div className='w-[50%]'>
                    {/* TITLE */}
                    <div className=''>
                      <h1 className='text-md text-left text-[#D9D9D9] select-none'>SKU:</h1>
                    </div>
                    {/* CONTENTS */}
                    <div className="min-h-[2rem] pb-[0.5rem]">
                      <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                        type="text"
                        value={sku}
                        onChange={e => setSKU(e.target.value)}  />
                    </div>
                  </div>
                  <div className='w-[50%]'>
                    {/* TITLE */}
                    <div className=''>
                      <h1 className='text-md text-left text-[#D9D9D9] select-none'>Image:</h1>
                    </div>
                    {/* CONTENTS */}
                    <div className="min-h-[2rem] pb-[0.5rem]">
                      <div className="w-full flex flex-col text-ato-form-text text-sm  justify-center px-3 py-2 font-universLight ">
                          <div>
                              <div 
                                  className="relative cursor-pointer"
                                  onClick={onClickPhoto}
                              >
                                  <div className='w-full h-[2rem] rounded-[0.6rem] m-auto border border-gray-300 text-gray-300 leading-[2rem]'>Upload</div>
                              </div>
                          </div>
                          <input
                              id="image_photo"
                              accept="image/*"
                              type="file"
                              onChange={onChangePhoto}
                              className="hidden"
                              name="avatar"
                          />
                      </div>
                    </div>
                  </div>
                </div>
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Update</button>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default EditProductModal;